














































































import { Component, Prop, Vue } from 'vue-property-decorator';

const instID:string="EDUNETMSP"
const bcID:string="edunetusr@edunet.net"
const peerID:string="peer0.edunet.net"
const apiBase:string="http://apihost:7001/api"

@Component
export default class Home extends Vue {
  email:string|null="";  
  name:string=""
  dob:string=""
  mode:string=""
  isBusy:boolean=false
  profile:any|null = null
  sprofile:any=null
  studDegreeList:any=new Array()

  mounted () {
    if(localStorage.getItem("email")){
        this.email=localStorage.getItem("email")
    }
  }
  get isWait(){
      return this.isBusy
  }
  loadRegPage():void{
        console.log("Register")
        console.log(this.email)
        this.mode="reg"
  }
  async registerStudent(){
        let args=new Array()
        var studentInfo:any={}
        studentInfo["name"]=this.name
        studentInfo["dob"]=this.dob
        studentInfo["email"]=this.email

        args.push(studentInfo)
        let request=this.getRequest("registerStudent",args)
        
        try{
            this.isBusy=true
            let result= await this.axios.post(apiBase+"/invoke",request)
            if (result.data  && result.data.payload ) {
                //let record=result.data.payload
                console.log(result.data)
                if ( result.data.isSuccess  && result.data.isSuccess === true){
                    alert("Save successful")
                }else{
                    alert(result.data.payload.messsage)
                }
            }else{
                alert("Error in executing the transaction")
            }
        }catch(err){
            console.log(err)
        }finally{
            this.isBusy=false
        }
  }
  
  async getStudentDegreeList(){
        this.mode="vc"
        this.studDegreeList=new Array()
        let args=new Array()
        args.push(this.email)
        let request=this.getRequest("searchDegreeByStudentID",args)
        
        try{
            this.isBusy=true
            let result= await this.axios.post(apiBase+"/query",request)
            //
            if (result.data  && result.data.payload && result.data.payload.payload) {
                console.log(result.data.payload.payload)
                //var record=result.data.payload.payload
                //this.sprofile=result.data.payload.payload
                this.studDegreeList=result.data.payload.payload
            }else{
                //alert("Record not found")
            }
        }catch(err){
            console.log(err)
        }finally{
            this.isBusy=false
        }
    }
  async viewProfile(){
        console.log("View Profile")
        this.mode="vp"
        let args=new Array()
        args.push(this.email)
        let request=this.getRequest("queryById",args)
        try{
            let result= await this.axios.post(apiBase+"/query",request)
            if (result.data  && result.data.payload && result.data.isSuccess && result.data.payload.payload) {
                //let record=result.data.payload
                console.log(result.data)
                //alert("Save successful")
                this.profile=result.data.payload.payload
                
                
            }
        }catch(err){
            console.log(err)
            alert(err)
        }finally{

        }

  }
  getRequest(funcName:string,args:any[]):any{
        let request:any= {}
        request["channelID"]="degreerecordchannel"
        request["ccId"]="degreerecordmgmt"
        request["userId"]=bcID
        request["peers"]=[peerID]
        request["funcName"]=funcName
        request["args"]=args
        return request
    }
}
